import {Component, OnInit} from '@angular/core';
import {DataService} from "../data.service";
import {Item} from "../item";

@Component({
  selector: 'app-shopping-item',
  templateUrl: './shopping-item.component.html',
  styleUrls: ['./shopping-item.component.css'],
  providers: [DataService]
})
export class ShoppingItemComponent implements OnInit {

  shoppingItemList: Item[] = [];
  selectedItem: Item;
  toggleForm: boolean = false;

  constructor(private dataService: DataService) {
  }

  getItems() {
    this.dataService.getShoppingItems()
      .subscribe((items: any) => {
        this.shoppingItemList = items;
      });
  }

  addItem(form) {
    let newItem: Item = {
      itemName: form.value.itemName,
      itemQuantity: form.value.itemQuantity,
      itemBought: false
    }
    this.dataService.addShoppingItem(newItem).subscribe(item => {
      console.log(item);
      this.getItems();
    })
  }

  deleteItem(id) {
    this.dataService.deleteShoppingItem(id).subscribe(data => {
      console.log(data);
      this.getItems();
    });
  }

  showEditForm(item) {
    this.selectedItem = item;
    this.toggleForm = !this.toggleForm;
  }

  updateItem(form) {
    let newItem: Item = {
      _id: this.selectedItem._id,
      itemName: form.value.itemName,
      itemQuantity: form.value.itemQuantity,
      itemBought: form.value.itemBought
    };
    this.dataService.updateShoppingItem(newItem).subscribe(item => {
      console.log(item);
      this.getItems();
    });
    this.toggleForm = !this.toggleForm;
  }

  updateCheck(item){
    item.itemBought = !item.itemBought;
    this.dataService.updateShoppingItem(item).subscribe(result => {
      console.log('updated');
      this.getItems();
    });
  }

  ngOnInit() {
    this.getItems();
  }

}



